<style lang="css">
</style>
<template lang="html">
  <div class="apply">
    <h4 class="title">出库</h4>
    <div class="main-apply">
        <div class="row">
          <div class="input-box col-xs-4">
            <p>出库单号</p>
            <div class="input-group w50">
              <input type="text" class="form-control" name="" placeholder="出库单号">
              <span class="input-group-btn">
                <button class="btn btn-default" type="button">查询</button>
              </span>
            </div>
          </div>
          <div class="input-box col-xs-4">
            <p>提货单号</p>
            <div class="input-group w50">
              <input type="text" class="form-control" name="" placeholder="提货单号">
              <span class="input-group-btn">
                <button class="btn btn-default" type="button">查询</button>
              </span>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="input-box col-xs-4">
            <p>登记日期</p>
            <div class="input-group">
              <input type="text" name="" class="form-control w30" value="" readonly>
              <input type="text" name="" class="form-control w30" value="" readonly>
            </div>
          </div>
          <div class="input-box col-xs-4">
            <p>提货日期</p>
            <div class="input-group">
              <input type="text" name="" class="form-control w30" value="" readonly>
              <input type="text" name="" class="form-control w30" value="" readonly>
            </div>
          </div>
        </div>
        <tablelist :titles="tableTitles" :trdatas="trDataArr" :suns="sunResult" @increment="saveBtn"></tablelist>
        <div class="table-box">
          <table class="table table-striped">
            <thead>
              <tr>
                <th v-for='titleBox in tableBoxTitles'>{{ titleBox }}</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td v-for='boxTrTxt in boxTrDataArr'>{{ boxTrTxt }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <th v-for='sun in suns'>{{ sun }}</th>
              </tr>
            </tfoot>
          </table>
        </div>
        <div class="btn-box" role="group">
          <button type="button" class="btn btn-default">新单据</button>
          <button type="button" class="btn btn-default">删除行</button>
          <button type="button" class="btn btn-default">增加行</button>
          <button type="button" class="btn btn-default" @click="selectProduct">选择产品</button>
          <button type="button" class="btn btn-default">保存</button>
          <button type="button" class="btn btn-default">打印出库单</button>
        </div>
    </div>
    <tabledialog></tabledialog>
  </div>
</template>

<script>
import tablelist from '../components/table-List'
import tabledialog from './table-dialog'
export default {
  name:'apply',
  data () {
    return {
      tableTitles:['产品代码','产品名称','提货数量','备注'],
      trDataArr:[31553,2,3,1],
      sunResult:['合计','',0,''],
      tableBoxTitles:['箱号','流转单号','产品代码','库存数量','提货数量','剩余数量'],
      boxTrDataArr:['001','1212122','31553','10000','3','9997'],
      suns:['合计','','',10000,3,9997]
    }
  },
  components: {
    tablelist,
    tabledialog
  },
  mounted(){
  },
  methods:{
    selectProduct(){
      this.$store.commit('toggleSelectDialog')
    }
  }
}
</script>



